import Vue from 'vue';
//配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter);
    // 解决多次点击左侧菜单报错问题
    const VueRouterPush = VueRouter.prototype.push
    VueRouter.prototype.push = function push (to) {
    return VueRouterPush.call(this, to).catch(err => err)
    }
//1.创建组件
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
import beifen from '@/views/modules/databaseBackup/beifen'
import huanyuan from '@/views/modules/databaseBackup/huanyuan'

     import users from '@/views/modules/users/list'
    import caozuorizhi from '@/views/modules/caozuorizhi/list'
    import dictionary from '@/views/modules/dictionary/list'
    import liuyan from '@/views/modules/liuyan/list'
    import news from '@/views/modules/news/list'
    import shiwuzhaoling from '@/views/modules/shiwuzhaoling/list'
    import shiwuzhaolingYuyue from '@/views/modules/shiwuzhaolingYuyue/list'
    import xunwuqishi from '@/views/modules/xunwuqishi/list'
    import xunwuqishiLiuyan from '@/views/modules/xunwuqishiLiuyan/list'
    import yonghu from '@/views/modules/yonghu/list'
    import config from '@/views/modules/config/list'
    import dictionaryJinyong from '@/views/modules/dictionaryJinyong/list'
    import dictionaryNews from '@/views/modules/dictionaryNews/list'
    import dictionaryRenlingzhuangtai from '@/views/modules/dictionaryRenlingzhuangtai/list'
    import dictionarySex from '@/views/modules/dictionarySex/list'
    import dictionaryShiwuzhaoling from '@/views/modules/dictionaryShiwuzhaoling/list'
    import dictionaryShiwuzhaolingYuyueYesno from '@/views/modules/dictionaryShiwuzhaolingYuyueYesno/list'
    import dictionaryZhaohuizhuangtai from '@/views/modules/dictionaryZhaohuizhuangtai/list'





//2.配置路由   注意：名字
const routes = [{
    path: '/index',
    name: '首页',
    component: Index,
    children: [{
      // 这里不设置值，是把main作为默认页面
      path: '/',
      name: '首页',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '修改密码',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '支付',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '个人信息',
      component: center,
      meta: {icon:'', title:'center'}
    }, {
        path: '/huanyuan',
        name: '数据还原',
        component: huanyuan
    }, {
        path: '/beifen',
        name: '数据备份',
        component: beifen
    }, {
        path: '/users',
        name: '管理信息',
        component: users
    }
    ,{
        path: '/dictionaryJinyong',
        name: '账户状态',
        component: dictionaryJinyong
    }
    ,{
        path: '/dictionaryNews',
        name: '公告类型',
        component: dictionaryNews
    }
    ,{
        path: '/dictionaryRenlingzhuangtai',
        name: '认领状态',
        component: dictionaryRenlingzhuangtai
    }
    ,{
        path: '/dictionarySex',
        name: '性别类型',
        component: dictionarySex
    }
    ,{
        path: '/dictionaryShiwuzhaoling',
        name: '物品类型',
        component: dictionaryShiwuzhaoling
    }
    ,{
        path: '/dictionaryShiwuzhaolingYuyueYesno',
        name: '审核',
        component: dictionaryShiwuzhaolingYuyueYesno
    }
    ,{
        path: '/dictionaryZhaohuizhuangtai',
        name: '找回状态',
        component: dictionaryZhaohuizhuangtai
    }
    ,{
        path: '/config',
        name: '轮播图',
        component: config
    }


    ,{
        path: '/caozuorizhi',
        name: '操作日志',
        component: caozuorizhi
      }
    ,{
        path: '/dictionary',
        name: '字典',
        component: dictionary
      }
    ,{
        path: '/liuyan',
        name: '留言板',
        component: liuyan
      }
    ,{
        path: '/news',
        name: '公告信息',
        component: news
      }
    ,{
        path: '/shiwuzhaoling',
        name: '失物信息',
        component: shiwuzhaoling
      }
    ,{
        path: '/shiwuzhaolingYuyue',
        name: '失物认领',
        component: shiwuzhaolingYuyue
      }
    ,{
        path: '/xunwuqishi',
        name: '寻物启事',
        component: xunwuqishi
      }
    ,{
        path: '/xunwuqishiLiuyan',
        name: '寻物启事留言',
        component: xunwuqishiLiuyan
      }
    ,{
        path: '/yonghu',
        name: '用户',
        component: yonghu
      }


    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '首页',
    redirect: '/index'
  }, /*默认跳转路由*/
  {
    path: '*',
    component: NotFound
  }
]
//3.实例化VueRouter  注意：名字
const router = new VueRouter({
  mode: 'hash',
  /*hash模式改为history*/
  routes // （缩写）相当于 routes: routes
})

export default router;
